import React,{Component} from 'react';
import SOCIAL from '../data/social'



class SocilaProfiles extends Component{ 
render(){
    return(
        <div className="subHeading"><h2>Social Profiles</h2>

<div  className="socialProfiles">
    {
    SOCIAL.map((s)=>this.dispalyContentent(s))
    }
</div></div>
    )
}


dispalyContentent(social){
return (
<div className="socialProfile">
<div >{social.title}</div>
<a href={social.link}><img src={social.image} alt="Profile image" className="socialProfileImage"></img></a>

</div>
)
}


}

export default SocilaProfiles;  